/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
  line-height: 2;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

.section {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .section {
    padding: 8rem 0;
  }
}

.site-header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../img/header.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  padding: 5rem 0;
}
.site-header .logo {
  max-width: 20rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .site-header .logo {
    margin: 0;
  }
}
.site-header .header-content {
  padding: 10rem 0;
}
@media (min-width: 1024px) {
  .site-header .header-content {
    padding: 15rem 0;
  }
}
.site-header .header-content h1 {
  color: #FFFFFF;
  font-size: 4.5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .site-header .header-content h1 {
    font-size: 6rem;
    max-width: 50%;
    text-align: left;
  }
}
@media (min-width: 1024px) {
  .site-header .header-content h1 {
    max-width: 40%;
  }
}

.button-primary, .button-plan {
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #313c4e;
  text-decoration: none;
  transition: background-color 300ms ease;
  border: none;
}
.button-primary:hover, .button-plan:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .button-primary, .button-plan {
    width: auto;
    display: inline-block;
  }
}

.button-plan {
  border: 2px solid #FFFFFF;
  margin-top: 3rem;
  display: block;
}

.create-grid {
  text-align: center;
}
@media (min-width: 768px) {
  .create-grid {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    text-align: left;
  }
}
.create-grid h2 {
  font-size: 3.6rem;
  display: inline-block;
  color: #313c4e;
  margin-top: 5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .create-grid h2 {
    margin-top: 0;
    text-align: left;
  }
}
.create-grid h2:after {
  content: "";
  background-color: #84d9bc;
  width: 100%;
  height: 1rem;
  display: block;
  margin: -1rem 0 0 0;
}
@media (min-width: 768px) {
  .create-grid h2:after {
    width: calc(100% + 1rem);
  }
}

.benefits {
  background-color: #84d9bc;
  text-align: center;
}
@media (min-width: 768px) {
  .benefits-grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(5, 1fr);
    align-items: center;
    text-align: left;
  }
}
.benefits-content {
  grid-column: 1/4;
}
.benefits-content h2 {
  font-size: 3.6rem;
  display: inline-block;
  color: #313c4e;
  margin-top: 5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .benefits-content h2 {
    margin-top: 0;
    text-align: left;
  }
}
.benefits-content h2:after {
  content: "";
  background-color: #FFFFFF;
  width: 100%;
  height: 1rem;
  display: block;
  margin: -1rem 0 0 0;
}
@media (min-width: 768px) {
  .benefits-content h2:after {
    width: calc(100% + 1rem);
  }
}
.benefits .image {
  grid-column: 4/6;
  transition: transform 300ms;
}
.benefits .image:hover {
  transform: rotate(8deg) scale(1.1);
}

.statistics {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}
@media (min-width: 768px) {
  .statistics {
    display: block;
    text-align: left;
  }
}
.statistics h2 {
  font-size: 3.6rem;
  display: inline-block;
  color: #313c4e;
  margin-top: 5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .statistics h2 {
    margin-top: 0;
    text-align: left;
  }
}
.statistics h2:after {
  content: "";
  background-color: #84d9bc;
  width: 100%;
  height: 1rem;
  display: block;
  margin: -1rem 0 0 0;
}
@media (min-width: 768px) {
  .statistics h2:after {
    width: calc(100% + 1rem);
  }
}

.plans {
  background-color: #313c4e;
  text-align: center;
}
@media (min-width: 768px) {
  .plans {
    text-align: left;
  }
}
.plans h2 {
  font-size: 3.6rem;
  display: inline-block;
  color: #FFFFFF;
  margin-top: 5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .plans h2 {
    margin-top: 0;
    text-align: left;
  }
}
.plans h2:after {
  content: "";
  background-color: #84d9bc;
  width: 100%;
  height: 1rem;
  display: block;
  margin: -1rem 0 0 0;
}
@media (min-width: 768px) {
  .plans h2:after {
    width: calc(100% + 1rem);
  }
}
.plans .description {
  color: #FFFFFF;
}

.our-plans {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .our-plans {
    flex-direction: row;
    max-width: 80rem;
    margin-inline: auto;
    margin-block-start: 10rem;
  }
}
.our-plans .plan {
  flex-grow: 1;
  background-color: #FFFFFF;
  padding: 16rem 4rem 4rem 4rem;
  border-bottom: 1px solid #E1E1E1;
}
@media (min-width: 768px) {
  .our-plans .plan {
    border: none;
    transition: transform 300ms;
  }
  .our-plans .plan:hover {
    transform: scale(1.1);
  }
}
.our-plans .plan.starter {
  background-image: url("../img/starter_icon.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
.our-plans .plan.business {
  order: -1;
  background-color: #84d9bc;
  background-image: url("../img/business_icon.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
@media (min-width: 768px) {
  .our-plans .plan.business {
    order: unset;
    margin-block: -5rem;
    border-radius: 2rem;
    background-position: 50% 7rem;
    padding-top: 21rem;
    z-index: 100;
  }
}
.our-plans .plan.pro {
  background-image: url("../img/pro_icon.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
.our-plans .plan .price {
  font-size: 1.8rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}
.our-plans .plan .list li {
  text-align: center;
  margin-bottom: 2rem;
}
.our-plans .plan .list li:last-of-type {
  margin-bottom: 0;
}

.testimonials {
  text-align: center;
}
@media (min-width: 768px) {
  .testimonials {
    text-align: left;
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(5, 1fr);
    align-items: center;
  }
  .testimonials .image {
    grid-column: 1/3;
  }
  .testimonials-content {
    grid-column: 3/6;
  }
}
.testimonials h2 {
  font-size: 3.6rem;
  display: inline-block;
  color: #313c4e;
  margin-top: 5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .testimonials h2 {
    margin-top: 0;
    text-align: left;
  }
}
.testimonials h2:after {
  content: "";
  background-color: #84d9bc;
  width: 100%;
  height: 1rem;
  display: block;
  margin: -1rem 0 0 0;
}
@media (min-width: 768px) {
  .testimonials h2:after {
    width: calc(100% + 1rem);
  }
}

.testimonials-content .testimonial {
  background-color: #84d9bc;
  border: 0.5rem solid #313c4e;
  margin-bottom: 4rem;
  padding: 2rem;
  border-radius: 1rem;
  position: relative;
  text-align: left;
}
@media (min-width: 768px) {
  .testimonials-content .testimonial {
    max-width: 70%;
  }
  .testimonials-content .testimonial:nth-child(2) {
    margin-left: 10%;
  }
  .testimonials-content .testimonial:nth-child(3) {
    margin-left: 20%;
  }
  .testimonials-content .testimonial:nth-child(4) {
    margin-left: 30%;
  }
}
.testimonials-content .testimonial::before {
  content: "";
  display: block;
  width: 5rem;
  height: 5rem;
  background-image: url(../img/quote.svg);
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
}
.testimonials-content .testimonial:last-of-type {
  margin-bottom: 0;
}
.testimonials-content .testimonial p {
  margin: 0;
  padding-left: 6rem;
}

.footer {
  background-color: #313c4e;
  color: #FFFFFF;
}
.footer .footer-grid {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .footer .footer-grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer .logo {
  max-width: 20rem;
  margin: 0 auto 5rem auto;
}
@media (min-width: 768px) {
  .footer .logo {
    margin-bottom: 0;
  }
}
.footer .footer-box {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .footer .footer-box {
    margin-bottom: 0;
  }
}
.footer .footer-box h3 {
  color: #84d9bc;
  text-align: center;
}
@media (min-width: 768px) {
  .footer .footer-box h3 {
    margin-bottom: 3rem;
  }
}
.footer nav a {
  display: block;
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 1rem;
  font-size: 2rem;
}
.footer nav a:last-of-type {
  margin-bottom: 0;
}
.footer nav a:hover {
  color: #e6e6e6;
}
.footer .copyright {
  margin: 0 auto;
  text-align: center;
  color: #FFFFFF;
  border-top: 2px solid #84d9bc;
  padding: 1rem;
  max-width: 120rem;
  font-size: 1.2rem;
  text-transform: uppercase;
}